}

:root {
  --background-color: #290505;
  --main-color: #e8834d;
  --secondary-color: #c26a3e;
  --text-color: #ae8873;
}

body {
  word-break: keep-all;
  text-rendering: optimizeLegibility;
  word-break: keep-all;
  margin: 0;
  font-family: pretendard, sans-serif;
  font-size: min(3vw, 16px);
  line-height: 1.5;
}

span {
  display: inline-block;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

ol, ul {
  list-style: none;
}

.wrapper {
  color: var(--text-color);
  background-color: var(--background-color);
  overflow-x: hidden;
}

.section .ct-section-inner-wrap {
  flex-direction: column;
  width: calc(100% - 12em);
  max-width: 1490px;
  margin: 0 auto;
  padding: 3em 0;
  display: flex;
}

.section--wide .ct-section-inner-wrap {
