  z-index: 1;
  justify-content: center;
  width: 100%;
  display: flex;
  position: fixed;
}

.header__wrapper {
  justify-content: center;
  display: flex;
}

.header__link {
  padding: 1em;
  display: block;
}

.aside {
  width: 3em;
  height: calc(100 * var(--vh, 1vh));
  justify-content: center;
  align-items: center;
  font-family: Fustat, Pretendard, sans-serif;
  display: flex;
  position: fixed;
  top: 0;
}

.aside--left {
  left: 0;
}

.aside--left .aside__wrapper {
  transform: rotate(-90deg);
}

.aside--right {
  right: 0;
}

.aside--right .aside__wrapper {
  transform: rotate(90deg);
}

.aside__wrapper {
  white-space: nowrap;
  gap: 2em;
  display: flex;
}

.footer {
  border-top: 1px dashed var(--main-color);
}

.footer .ct-section-inner-wrap {
  text-transform: capitalize;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 6em 0;
  font-family: Fustat, Pretendard, sans-serif;
