  flex: 0 0 50%;
  font-size: 7em;
  font-weight: 500;
  line-height: 1.1;
  display: flex;
}

.footer__span {
  display: block;
}

.footer__span:nth-child(2) {
  text-align: right;
}

.footer__info {
  flex-direction: column;
  flex: 0 0 16.667%;
}

.footer__site-name {
  color: var(--main-color);
  margin-bottom: 1em;
  font-size: 1.25em;
}

.footer__link {
  font-size: 2.5em;
}

.footer__link-wrapper {
  flex-direction: column;
}

.loading {
