  width: calc(100% - 8em);
  margin: 0 auto;
  position: relative;
}

.home-intro__main-typo {
  width: 100%;
  position: absolute;
}

.home-intro__content {
  height: calc(100 * var(--vh, 1vh));
  box-sizing: border-box;
  z-index: 0;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  max-width: 1490px;
  margin: 0 auto;
  padding: 4em 0;
