}

.home-intro__sub-copy {
  color: var(--main-color);
  text-align: right;
  flex: 0 0 10%;
}

.home-introduce__row {
  flex-direction: column;
}

.home-introduce__row--profile {
  flex: 0 0 50%;
}

.home-introduce__row--content {
  flex: 0 0 30%;
}

.home-introduce__row h2 {
  color: var(--secondary-color);
  margin-bottom: 1em;
  font-size: 1.75em;
  font-weight: 700;
}

.home-introduce__row p {
  margin-bottom: 1em;
}

.home-title {
  align-items: center;
  gap: 2.25em;
  font-family: Fustat, Pretendard, sans-serif;
  display: flex;
}

.home-title__head {
  color: var(--main-color);
  font-size: 8em;
  font-weight: 500;
}

.home-title__body {
  color: var(--secondary-color);
