  font-weight: 700;
}

.home-competencies {
  flex-direction: column;
  gap: 4em;
  width: 100%;
  font-family: Fustat, Pretendard, sans-serif;
  display: flex;
}

.home-competencies__item {
  opacity: .4;
  gap: 1.5em;
  width: 100%;
  display: flex;
}

.home-competencies__index {
  flex: 0 0 10%;
  font-size: 1.75em;
  font-weight: 200;
}

.home-competencies__title {
  text-transform: capitalize;
  color: var(--secondary-color);
  flex: 0 0 40%;
  font-size: 4em;
  font-weight: 500;
  line-height: 1;
}

.home-competencies__desc {
  flex: 0 0 30%;
  font-size: 1.25em;
}

.home-project {
  width: 100%;
  display: flex;
  overflow: hidden;
}

@keyframes loop-gallery {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-50%);
  }
}

.home-project__wrapper {
  gap: 0;
  width: auto;
  animation: 20s linear infinite loop-gallery;
  display: flex;
}

.home-project__item {
  aspect-ratio: 2 / 3;
  box-sizing: content-box;
  width: 22.125em;
  padding: 0 .75em;
}

.home-project__item img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.home-project__item-container {
  gap: 0;
  width: 100%;
  display: flex;
}

.home__content {
  width: calc(100% - 8em);
  max-width: 1490px;
  margin: 0 auto;
  padding: 192px 0;
}

.home__content-wrapper {
  flex-direction: column;
  gap: 8em 1.5em;
  width: 100%;
  display: flex;
}

.home__content-wrapper--row {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.works > .ct-section-inner-wrap {
  padding: 8em 0 12em;
}

.works__wrapper {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 1em;
  display: grid;
}

@media screen and (width >= 1280px) {
  .works__wrapper {
    grid-template-columns: repeat(10, 1fr);
    gap: min(128px, 6vw);
  }
}

@media screen and (width >= 1028px) and (width <= 1279px) {
  .works__wrapper {
    gap: 64px;
  }
}

.works-card {
  border-radius: 1em;
  width: 100%;
  height: fit-content;
  padding-bottom: 120%;
  position: relative;
  overflow: hidden;
}

@media screen and (width >= 1280px) {
  .works-card {
    grid-column: span 4 / span 4;
  }

  .works-card:nth-child(4n) {
    grid-column-end: 12;
  }

  .works-card:nth-child(6n-3) {
    grid-column-start: 3;
  }

  .works-card:nth-child(6n-5) {
    grid-column-start: 2;
  }

  .works-card:nth-child(4n-2) {
    grid-column-end: 11;
  }
}

.works-card__content {
  box-sizing: border-box;
  width: 100%;
  color: var(--secondary-color);
  background-image: linear-gradient(180deg, var(--background-color), transparent);
  text-transform: capitalize;
  flex-direction: column;
  gap: .75em;
  padding: 2em 2em 10em;
  display: flex;
  position: absolute;
  top: 0;
}

.works-card__background {
  object-fit: cover;
  width: 100%;
  height: 150%;
  position: absolute;
  top: 25%;
}

.works-card__info {
  justify-content: space-between;
  align-items: center;
  gap: 1em;
  display: flex;
}

.works-card__info-item {
  flex-shrink: 0;
}

.works-card__hr {
  background-color: var(--secondary-color);
  border: 0;
  outline: 0;
  width: 100%;
  height: .5px;
}

.works-card__title {
  text-align: left;
  word-break: keep-all;
  color: var(--main-color);
  font-size: 1.5em;
  font-weight: 700;
}

